import React from "react";

const SocialNetwork = () => {
  const anim = () => {
    const icons = document.querySelectorAll(".social-network a");
    icons.forEach((link) => {
      link.addEventListener("mouseover", (e) => {
        link.style.transform = `translate(${e.offsetX - 20}px, ${
          e.offsetY - 13
        }px)`;
      });
      link.addEventListener("mouseleave", () => {
        link.style.transform = "";
      });
    });
  };

  return (
    <div className="social-network">
      <ul className="content">
        <a
          href="https://fr-fr.facebook.com/restaurantamaranthe"
          target="_blank"
          rel="noopener noreferrer"
          className="hover"
          onMouseOver={anim}
        >
          <li>
            <i className="fab fa-facebook-f"></i>
          </li>
        </a>
        <a
          href="https://www.tripadvisor.fr/Restaurant_Review-g425098-d7249316-Reviews-L_Amaranthe_a_L_Auberge_de_la_Charente-Port_d_Envaux_Charente_Maritime_Nouvelle_A.html"
          target="_blank"
          rel="noopener noreferrer"
          className="hover"
          onMouseOver={anim}
        >
          <li>
            <i>
              <img
                src="./images/icon-tripadvisor.png"
                alt="icon"
                id="tripadvisor"
                style={{ width: "40px" }}
              />
            </i>
          </li>
        </a>
        <a
          href="https://www.instagram.com/amaranthe.restaurant/?hl=fr"
          target="_blank"
          rel="noopener noreferrer"
          className="hover"
          onMouseOver={anim}
        >
          <li>
            <i className="fab fa-instagram"></i>
          </li>
        </a>
      </ul>
    </div>
  );
};

export default SocialNetwork;
